import React, {useState} from 'react';
import { makeStyles } from "@material-ui/core";
import urlData from './videosURL';
import ReactPlayer from 'react-player';
import { Grid } from '@material-ui/core';

import { useTheme } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';


const useStyles = makeStyles((theme) => ({
    containerStyles: {
        [theme.breakpoints.up('sm')]: {
            padding: "1em 1em 1em 5em",
        },
    },
    headerStyles: {
      fontSize: "1.3em",
      fontWeight: "bold",
    },
    mainContainer:{
        display: "flex",
    },
    videosContainer:{
        display: "flex",
        flexWrap: "wrap",
        // border: "1px solid #ccc"
    },
    videosContainer2:{
        display: "flex",
        flexWrap: "wrap",
        // justifyContent: "space-between"
        // border: "1px solid #ccc"
    },
    mainContainer:{
        display: "flex",
        flexDirection: "column",
    },
    videosLists:{
        display: "flex",
        flexWrap: "wrap",
        height:"500px",
        overflowX: "hidden",
        overflowY: "scroll",
        justifyContent: "center"
    },
    videoStyles: {
       padding: "0.5em 3em 0.5em 0"
    },
    videoName:{
        fontSize: "1em",
        paddingBottom: "0.8em",
        fontWeight: "bold",
    },
    video:{
        position: "relative",
        padding:"5px"
    },
    onHover:{
        display:"flex",
        justifyContent: "center",
        alignItems: "center",
        position: "absolute",
        top: 0,
        width: "100%",
        [theme.breakpoints.up('sm')]: {
            width: "280px",
        },
        height: "100%",
        color: "white",
        '&:hover':{
            cursor: "pointer"
        }
    },
    onHover2:{
        display:"flex",
        justifyContent: "center",
        alignItems: "center",
        position: "absolute",
        top: 0,
        width: "100%",
        [theme.breakpoints.up('sm')]: {
            width: "405px",
        },
        height: "100%",
        color: "white",
        '&:hover':{
            cursor: "pointer",
        }
    },
    vdContainer:{
        overflow:"hidden"
    }
  }));

function VideosListPage(){
    const classes = useStyles();
    const [currentVideo, setCurrentVideo] = useState();

    const theme = useTheme();
    const matches = useMediaQuery(theme.breakpoints.up('sm'));

    const handleClick = (e) =>{
        let newVideo = e.target.getAttribute('videonumber')
        setCurrentVideo(newVideo);
    }

    return (
        <div className={classes.vdContainer}>
        {(!currentVideo)?
        <div className={classes.containerStyles}>
                <p className={classes.headerStyles}>Gems of Bappi Lahiri</p>
                <Grid container spacing={1} >
                    {urlData.map((video, index) => {
                        let newURL = video.url
                        let name = video.name;
                        return (
                            <Grid item onClick={handleClick} className={classes.video} xs={12} md={4}>
                                <p className={classes.videoName}>{name}</p>
                                <ReactPlayer light width= {matches ? "400px" : "100%"} height="220px" url={newURL}/>
                                <div className={classes.onHover2} videonumber={index}></div>
                            </Grid>
                        )
                    })}
                </Grid>
            </div>:
            <div className={classes.containerStyles}>
             <p className={classes.headerStyles}>Video Guide List</p>
                <div>
                    <div>
                        <p className={classes.videoName}>{urlData[currentVideo].name}</p>
                        <ReactPlayer controls playing width={matches ? "860px" : "100%"} height={matches ? "490px" : "100%"} url={urlData[currentVideo].url}/>
                    </div>
                    <div>
                            <h4 style={{paddingBottom: "1em"}}>Web Platform Tutorials</h4>
                            <div className={classes.videosLists}>
                                {urlData.map((video, index) => {
                                    let newURL = video.url
                                    let name = video.name;
                                    return (
                                        <div onClick={handleClick} className={classes.video}>
                                            <p className={classes.videoName}>{name}</p>
                                            <ReactPlayer light width="280px" height="140px" url={newURL}/>
                                            <div className={classes.onHover} videonumber={index}></div>
                                        </div>
                                    )
                                })}
                            </div>
                    </div>
                </div>
            </div>
        }
        </div>
    )
}

export default VideosListPage;